@import url(https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,700;1,700&family=Sedgwick+Ave+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Nav-component {
    background-color: rgb(241, 241, 241);
    width: 100%;
    position: fixed;
    top: 0;
    opacity: 90%;
}

.Nav-logo {
    display: inline;
    padding-left: 20px;
}

.Nav-text {
    height: 50px;
    margin: 30px 20px 20px 20px;
}

.Nav-objects {
    float: right;
    padding-right: 40px;
}







.test {
    font-family: 'Lato', sans-serif;
    font-size: 45px;
    color: rgb(61 63 119);
    padding: 0px 100px 0px 100px;
}

.Hello-text {
    font-family: 'Sedgwick Ave Display', cursive;
    font-size: 90px;
    text-align: center;
    margin: 50px 0px;
}

.Chandler-headshot {
    width: 50%;
    max-width: 700px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.Page-under-construction {
  width: 80%;
  padding-top: 50px;
  max-width: 1200px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.Navbar-overlap-padding {
  padding: 105px 0px;
}

